.loading {
  rotate: -90deg;
  scale: -1 1;
}
.loading.default {
  width: 55px;
  height: 55px;
}
.loading.default .circle {
  width: 100%;
  height: 100%;
  fill: transparent;
  transform-origin: center;
  stroke: #828282;
  stroke-linecap: round;
  transform: translate(2.5px, 2.5px);
  stroke-width: 5px;
  stroke-dasharray: 157.5px;
  animation-duration: 1.25s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
@keyframes defaultAnimation {
  from {
    stroke-dashoffset: 30px;
    rotate: 0;
  }
  50% {
    stroke-dashoffset: 135px;
    rotate: -240deg;
  }
  to {
    stroke-dashoffset: 30px;
    rotate: -720deg;
  }
}
@keyframes defaultAnimationDelay {
  from {
    stroke-dashoffset: 30px;
    rotate: 0;
  }
  50% {
    stroke-dashoffset: 100px;
    rotate: -240deg;
  }
  to {
    stroke-dashoffset: 30px;
    rotate: -720deg;
  }
}
.loading.default .circle:first-child {
  stroke-opacity: 0.5;
  animation-name: defaultAnimationDelay;
}
.loading.default .circle:last-child {
  animation-name: defaultAnimation;
}
.loading.inButton {
  width: 11px;
  height: 11px;
}
.loading.inButton .circle {
  width: 100%;
  height: 100%;
  fill: transparent;
  transform-origin: center;
  stroke: #828282;
  stroke-linecap: round;
  transform: translate(0.5px, 0.5px);
  stroke-width: 1px;
  stroke-dasharray: 31.5px;
  animation-duration: 1.25s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
@keyframes inButtonAnimation {
  from {
    stroke-dashoffset: 6px;
    rotate: 0;
  }
  50% {
    stroke-dashoffset: 27px;
    rotate: -240deg;
  }
  to {
    stroke-dashoffset: 6px;
    rotate: -720deg;
  }
}
@keyframes inButtonAnimationDelay {
  from {
    stroke-dashoffset: 6px;
    rotate: 0;
  }
  50% {
    stroke-dashoffset: 20px;
    rotate: -240deg;
  }
  to {
    stroke-dashoffset: 6px;
    rotate: -720deg;
  }
}
.loading.inButton .circle:first-child {
  stroke-opacity: 0.5;
  animation-name: inButtonAnimationDelay;
}
.loading.inButton .circle:last-child {
  animation-name: inButtonAnimation;
}

