* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #E7E7E7;
  padding: 15px 20px;
  min-width: 300px;
  min-height: 400px;
}

.App {
  font-family: "Helvetica Neue", sans-serif;
}

h1 {
  font-size: 24px;
}

h2 {
  font-size: 10px;
  text-transform: uppercase;
  color: #828282;
}

.Name {
  font-size: 12px;
  font-weight: normal;
  line-height: 14px;
  color: #343434
}

.Description {
  font-size: 8px;
  font-weight: 500;
  color: #828282;
  &.clickable {
    display: flex;
    flex-direction: column;
  }
  &.clickable::after {
    content: "";
    height: 1px;
    margin-top: 1px;
    border-radius: 1px;
    width: 100%;
    background-color: #828282;
    transform: scaleX(0);
    transform-origin: center;
    transition: transform 0.3s ease;
  }
  &.clickable:hover::after {
    transform: scaleX(1);
  }
}

.Version {
  font-size: 12px;
  font-weight: 500;
  line-height: 15px;
  text-align: center;
  color: #828282;
}

.Menus, .ActivationMenu {
  margin-top: 35px;
}

.Menu {
  margin-bottom: 35px;
  & > .Container {
    margin-top: 5px;
    display: flex;
    flex-direction: column;
    padding: 0 15px;
    background-color: #D9D9D9;
    border-radius: 10px;
  }
}

.Separator {
  width: 100%;
  height: 1px;
  opacity: 1;
  background-color: #C1C1C1;
  border-radius: 1px;
  transition: opacity 0.4s ease;
  will-change: opacity;
  &.hidden {
    opacity: 0;
  }
}

%flexProperties {
  display: flex;
  align-items: center;
}

.MenuItem {
  @extend %flexProperties;
  justify-content: space-between;
  flex-direction: row;
  margin: 10px 0;
  & > .Container {
    @extend %flexProperties;
    flex-direction: row;
    .Icon {
      @extend %flexProperties;
      justify-content: center;
      margin-right: 5px;
      height: 30px;
      width: 30px;
      background-color: #828282;
      border-radius: 7px;
    }
    .Info {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
  }
}

#Pause .Icon {
  background-color: #7043F0;
}

#Eye .Icon {
  background-color: #F5A123;
  .ColorLine {
    background-color: #F5A123;
  }
}

.LinesContainer {
  rotate: -45deg;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  .WhiteLine, .ColorLine {
    height: 2px;
    width: 22px;
    border-radius: 1px;
    transform: scaleX(0);
    transform-origin: right;
    transition: 0.2s linear;
    &.enable {
      transform: scaleX(1);
    }
  }
}

.WhiteLine {
  background-color: #F3F3F3;
}

.ContextMenu {
  overflow: hidden;
  height: 0;
  opacity: 0;
  transition: height 0.6s ease, opacity 0.4s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
  will-change: height, opacity;
  &.open {
    height: 30px;
    opacity: 1;
    &.extended {
      height: 38px;
    }
  }
}

.clickable {
  cursor: pointer;
}

.overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(#000, 0.4);
  position: absolute;
  top: 0; left: 0; bottom: 0; right: 0;
  z-index: 5;
  display: flex;
  justify-content: center;
  align-items: center;
  .modal {
    height: 200px;
    width: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 10px;
    background-color: #D9D9D9;
    .control {
      margin: 8px 0;
      padding: 0 10px;
      width: 100%;
    }
  }
}

.loadingWrapper {
  position: absolute;
  left: 0; right: 0; top: 0; bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.errorText {
  color: #626262;
  font-size: 10px;
  font-weight: normal;
}
