.Button {
  cursor: pointer;
  height: 13px;
  width: 13px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0.5px solid #d94848;
  border-radius: 50px;
  background-color: #f56d6d;
  transition: background-color 0.15s ease;
}
.Button:active {
  background-color: #fd8787;
}
.Button .Cross {
  margin: 1px;
  opacity: 0;
  transition: opacity 0.15s ease;
}
.Button .Cross.show {
  opacity: 1;
}

