.Switch {
    position: relative;
    display: inline-block;
    width: 35px;
    height: 20px;
}

.Switch input {display:none;}

.Slider {
    position: absolute;
    border-radius: 10px;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #B2B2B2;
    box-shadow: inset 1px 1px 3px rgba(0, 0, 0, 15%);
    transition: .4s;
}

.Slider:before {
    position: absolute;
    border-radius: 50%;
    content: "";
    height: 17px;
    width: 17px;
    left: 1.5px;
    bottom: 1.5px;
    background-color: #F3F3F3;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 13%);
    transition: .4s;
}

input:checked + .Slider {
    background-color: #24C716;
}

input:checked + .Slider:before {
    transform: translateX(15px);
}
