@mixin animation($name, $max-dashoffset, $circle-size) {
  @keyframes #{$name} {
    from {
      stroke-dashoffset: 60 * $circle-size / 100;
      rotate: 0;
    }
    50% {
      stroke-dashoffset: $max-dashoffset * $circle-size / 100;
      rotate: -240deg;
    }
    to {
      stroke-dashoffset: 60 * $circle-size / 100;
      rotate: -720deg;
    }
  }
}

@mixin sizes($circle-size, $stroke-width) {
  width: $circle-size + $stroke-width;
  height: $circle-size + $stroke-width;
}

@mixin circleProperties($circle-size, $stroke-width, $animation-name, $animation-name-delay) {
  width: 100%;
  height: 100%;
  fill: transparent;
  transform-origin: center;
  stroke: #828282;
  stroke-linecap: round;
  transform: translate($stroke-width / 2, $stroke-width / 2);
  stroke-width: $stroke-width;
  stroke-dasharray: 315 * $circle-size / 100;
  animation-duration: 1.25s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  &:first-child {
    stroke-opacity: 0.5;
    animation-name: $animation-name-delay;
  }
  &:last-child {
    animation-name: $animation-name;
  }
}

@mixin loadingIncludes($circle-size, $animation-name, $animation-name-delay) {
  @include sizes($circle-size, $circle-size / 10);
  .circle {
    @include animation($animation-name, 270, $circle-size);
    @include animation($animation-name-delay, 200, $circle-size);
    @include circleProperties($circle-size, $circle-size / 10, $animation-name, $animation-name-delay);
  }
}

.loading {
  &.default {
    $circle-size: 50px;
    $animation-name: defaultAnimation;
    $animation-name-delay: defaultAnimationDelay;
    @include loadingIncludes($circle-size, $animation-name, $animation-name-delay);
  }
  &.inButton {
    $circle-size: 10px;
    $animation-name: inButtonAnimation;
    $animation-name-delay: inButtonAnimationDelay;
    @include loadingIncludes($circle-size, $animation-name, $animation-name-delay)
  }
  rotate: -90deg;
  scale: -1 1;
}