.SubmitForm {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.Form {
  font-family: monospace;
  height: 20px;
  width: 175px;
  border: none;
  outline: none;
  padding: 0 0 0 10px;
  font-size: 10px;
  color: #343434;
  background-color: #B2B2B2;
  border-radius: 8px 0 0 8px;
}

.Button {
  height: 20px;
  min-width: 25px;
  border: none;
  outline: none;
  border-radius: 0 8px 8px 0;
  font-size: 10px;
  text-align: center;
  background-color: #007ae1;
  transition: 0.3s;
}
.Button .childContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 5px 0 4px;
}
.Button .childContainer .Checkmark {
  height: 16px;
  filter: invert(99%) sepia(5%) saturate(575%) hue-rotate(244deg) brightness(116%) contrast(91%);
}
.Button:active {
  background-color: #3b9ef4;
}
.Button:disabled {
  background-color: #aaa;
}
.Button:disabled .childContainer .Checkmark {
  filter: invert(45%) sepia(0%) saturate(0%) hue-rotate(61deg) brightness(94%) contrast(90%);
}

