.UpperPart, .LowerPart {
    width: 10px;
    height: 1.7px;
    background-color: #828282;
}

.UpperPart {
    transform: rotate(45deg);
    margin-bottom: 5px;
    border-radius: 1px 1px 0 1px;
}

.LowerPart {
    transform: rotate(-45deg);
    margin-top: 5px;
    border-radius: 1px 0 1px 1px;
}

.Arrow {
    transition: 0.4s ease;
}

.Open {
    transform: rotate(90deg);
}