.activationAnimate-enter, .menusAnimate-enter {
  opacity: 0;
  transform: scale(0.9);
}
.activationAnimate-enter-active, .menusAnimate-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 400ms ease, transform 400ms ease;
}
.activationAnimate-exit, .menusAnimate-exit {
  opacity: 1;
}
.activationAnimate-exit-active, .menusAnimate-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 400ms ease, transform 400ms ease;
}

.modalAnimate-enter, .errorAnimate-enter {
  opacity: 0;
}

.modalAnimate-enter-active, .errorAnimate-enter-active {
  opacity: 1;
  transition: opacity 0.4s ease-in-out;
}

.modalAnimate-exit, .errorAnimate-exit {
  opacity: 1;
}

.modalAnimate-exit-active, .errorAnimate-exit-active {
  opacity: 0;
  transition: opacity 0.4s ease-in-out;
}